import React from "react";
import { BurgerNavContainer, Icon, CloseIcon,BurgerWrapper, BurgerMenu, BurgerLink } from "./BurgerElements";

const BurgerNav = ({isOpen, toggle}) => { 
    return (
        <BurgerNavContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon></CloseIcon>
            </Icon>
            <BurgerWrapper>
                <BurgerMenu>
                    <BurgerLink to="register" onClick={toggle}>Register</BurgerLink>
                    <BurgerLink to='Teams' onClick={toggle}>Teams</BurgerLink>
                    <BurgerLink to='Legacy' onClick={toggle}>Legacy</BurgerLink>
                    <BurgerLink to='rules' onClick={toggle}>Team Rules</BurgerLink>
                    <BurgerLink to='Contact' onClick={toggle}>Contact</BurgerLink>
                </BurgerMenu>
            </BurgerWrapper>
        </BurgerNavContainer>
    );
}

export default BurgerNav;