import React from "react";
import '../sass/style.scss';
import 'bootstrap/dist/css/bootstrap.css';
import { Container, Row, Col } from 'react-bootstrap';
import map1 from '../images/googlemaps-1.png';



const Contact = () => {
    return (
        <div className="contact-body">
    
        <Container fluid id="contact-col-1">
            <Row>
                <Col>
                <div className="contact-heading">
        <p id="contact-title">Contact</p>
        <p id="contact-tel">Email : Birminghamimpact@gmail.com
        <br/>
        Tel : +44[0]7932 357 478
        </p>
        </div>
        </Col>

        <Col className="contact-form-space">
                <div className="form-container">
        <form>
            <div className="row">
            <div className="column">
                <input type="text" className="contact-bx" placeholder="NAME" name="name"/>

  
            <input type="email" className="contact-bx" placeholder="EMAIL" name="email"/>
            </div>
            

            <div className="column">
                <input type="text" className="contact-bx" placeholder="PHONE" name="name"/>

                <textarea className="contact-bx"  placeholder="MESSAGE" name="message"/>
            </div>

            <div className="btn-container">
                <input type="submit" id="gld-btn" value="SEND"></input>
            </div>
            </div>
            </form>
        </div>
        </Col>
                </Row>
                </Container>

                <Container fluid id="map">
                    <div className="map-heading">
                    <h1>WHERE WE PLAY</h1>
                    </div>
                    <Row>
                        <div className="col-sm-6">
                        <img src={map1} alt="map of moseley" className="map-img"  />
                        </div>
                        <div className="col-sm-6">
                        <h2 id="map-date">SATURDAY | 10am to 12pm</h2>
                        <p id="map-location">
                        Birmingham Moseley Rugby Club<br />
                        Billesley Common<br />
                        Yardley Wood Road<br />
                        Billesley, B13 0HN</p>
                        </div>
                    </Row>
                    <Row className="map-park">
                    <div className="col-sm-6">
                        <img src={map1} alt="map of moseley" className="map-img"  />
                        </div>
                        <div className="col-sm-6">
                        <h2 id="map-date">SATURDAY | 1pm to 3pm</h2>
                        <p id="map-location">
                        Recreational Park<br />
                        St Margarets Road<br />
                        Washwood Heath<br />
                        Birmingham, B8</p>
                        </div>
                    </Row>
                    <Row id="map-space">
                        <div className="col-sm-6">
                        <img src={map1} alt="map of moseley" className="map-img"  />
                        </div>
                        <div className="col-sm-6">
                        <h2 id="map-date">WEDNESDAY | 6pm to 7:30pm</h2>
                        <p id="map-location">
                        Saltley Wellbeing Centre<br />
                        120 Broadway Avenue<br />
                        Birmigham, B9 5YD</p>
                        </div>
                    </Row>
                    
                </Container>
        </div>
    )
};

export default Contact;